import { createSlice } from "@reduxjs/toolkit";

const order = createSlice({
  name: "order",
  initialState: {
    status: 'NEW',
    step: 0,
  },
  reducers: {
    setStep: (state, action) => {
      return state = Object.assign({}, state, action.payload)
    },
    setStatus: (state, action) => {
      return state = Object.assign({}, state, action.payload)
      // state.status = action.payload.status,
      // state.result = action.payload.result,
    },
    setOrder: (state, action) => {
      // Object.entries(action.payload).map(item => state = console.log('item', action.payload[item[0]]))
      // Object.entries(action.payload).map(item => state = { ...state, [item[0]]: action.payload[item[0]] })
      return state = Object.assign({}, state, action.payload)
    }
  }
})

const { actions, reducer } = order;
export const { setStep, setStatus, setOrder } = actions
export default reducer