import { createSlice } from "@reduxjs/toolkit";

const coborrower = createSlice({
  name: "coborrower",
  initialState: {
    // status: '',
  },
  reducers: {
    setStatus: (state, action) => {
      return state = Object.assign({}, state, action.payload)
    },
    setCoborrower: (state, action) => {
      return state = Object.assign({}, state, action.payload)
    }
  }
})

const { actions, reducer } = coborrower;
export const { setStatus, setCoborrower } = actions
export default reducer