import React from "react";
import { Router } from "react-router-dom";
import Routes from "./router";
import { history } from "@/helpers";

class Root extends React.Component {
  constructor(props) {
    super(props);  
  }

  render() {
    return (
      <Router history={history}>
        <Routes />
      </Router>
    );
  }
}

export { Root };
