import React, { Suspense } from "react";
import { Route } from "react-router-dom";
import PaymentError from "@/views/pages/error/page";

let cache = new Map();

function getPage(fileName) {
  let Page = cache.get(fileName) || {};
  if (!cache.has(fileName)) {
    Page = React.lazy(() => import(`@/views/pages/${fileName}/page`));
    cache.set(fileName, Page);
  }

  return (
    <Suspense fallback={<h2>  ...</h2>}>
      <Page />
    </Suspense>
  );
}

function HomePage() {
  return getPage("homePage");
}

function ResultPage() {
  return getPage("result");
}

function PaymentWithoutSms() {
  return getPage("paymentWithoutSms");
}

function PaymentPage() {
  return getPage("paymentPage");
}

export default function () {
  return (
    <div>
      <Route path="/" component={HomePage} exact />
      <Route path="/result" component={ResultPage} exact />
      <Route path="/payment-without-sms" component={PaymentWithoutSms} />
      <Route path="/payment" component={PaymentPage} />
      <Route path="/error" component={PaymentError} />
    </div>
  );
}
