import config from 'config';
import { history } from "@/helpers";

const toString = query => {
    const toJSON = param => typeof param === `object` ? JSON.stringify(param) : param
    const urify = param => toJSON(param)
    return Object.keys(query)
        .map(key => `${urify(key)}=${encodeURIComponent(urify(query[key]))}`)
        .join(`&`)
}

const checkStatus = response => {
    if (response.status < 400) {
        return response
    }
    throw response
}

const parseJSON = response => {
    if ([204, 205, 304].includes(response.status)) {
        return null
    }
    return response.json()
}




export const CALL_API = Symbol('Call API');


export default store => next => action => {

    const callAPI = action[CALL_API];



    if (typeof callAPI === 'undefined') {
        return next(action)
    }

    let { endpoint, actionType, method, params, channel = null } = callAPI;

    const token = localStorage.getItem('user') || null;
    
    let uri = `${config.apiUrl}/${endpoint}`;

    const opts = {
        headers: {
            'Content-Type': `application/json; charset=utf-8`,
            'Authorization': `JWT ${token}`,
            'Channel': channel
        },
        method
    };


    if (/get|delete/.test(method) && params) {
        uri = `${config.apiUrl}/${endpoint}?${toString(params)}`
    }

    if (/post|put/.test(method)) {
        opts.body = JSON.stringify(params)
    }


    // next({
    //     type: requestConstants.REQUEST_BEGIN
    // });



    return fetch(uri, opts).then(checkStatus).then(parseJSON).then(
        response => {
            let action = {
                data: response,
                params,
                type: actionType
            };

            // next({
            //     type: requestConstants.REQUEST_END
            // });
            if (actionType) {
                next(action);
                return action;
            } else {
                return action;
            }
        },
        error => {
            switch (error.status) {
                case 401:
                    // store.dispatch(alertActions.error('     .', true))
                    history.push('/login')
                    break;
                case 403:
                    // error.json().then((res) => store.dispatch(alertActions.error(res.detail, true)))
                    break;
            }
            //store.dispatch(requestFail(error));
            // next({
            //     data: error,
            //     type: requestConstants.REQUEST_FAIL
            // });
            return Promise.reject({
                data: error,
            });
        }
    ).then((err) => err)
}
