import { createSlice } from "@reduxjs/toolkit";

const aside = createSlice({
    name: "aside",
    initialState: {
        isAsideOpen: window.screen.width <= 768 ? true : false,
        asideStatus: 1,
        user: {}
    },
    reducers: {
        setStateAside: (state, action) => {
            state.isAsideOpen = action.isAsideOpen
        },
        setStatusAside: (state, action) => {
            state.asideStatus = action.asideStatus
        },
        setJWT: (state, action) => {
            state.user = action.payload
        },
    }
})

const { actions, reducer } = aside;
export const { setStateAside, setStatusAside, setJWT } = actions
export default reducer